/**
 * 
 * @param {*} urlType 
 * @param {} url 
 * @param {} type 
 * @param {} date 
 * @param {token} header 
 * @param (status === 0 ) 
 * @param (status === 1 ) 
 * @param (status === 2 tokentokentoken)
 *
 *
 */
// token
const loginPage = '/pages/login/index'
 
// 
const msg = '...'
const baseURL = 'https://chat.zclz.vip' //
// const baseURL = 'http://localhost:8080' //
const request = (urlType= '', url = '', type = '', date = {}, header = {
}) => {
	var that = this
	if(JSON.stringify(header)=='{}'){
		header={
			'Content-type': 'application/json',
			'token': uni.getStorageSync('token')			
		}
	}
    return new Promise((resolve, reject) => {
        uni.request({
            method: type,
            url: baseURL + url,
            data: date,
            header: header,
            dataType: 'json'         
        }).then((response) => {
			console.log(response)
			// uni.hideLoading();
			if (response[1].data.status == 0) {
				uni.showToast({
				    title: response[1].data.msg,
					icon: 'error',
				    duration: 2000
				});
				let [error, res] = response;
				resolve(res.data);
			} else if (response[1].data.status == 2) {
				uni.showToast({
				    title: '',
					icon: 'error',
				    duration: 2000,
					success: () => {
					}
				});
			} else {
				let [error, res] = response;
				resolve(res.data);
			}
        }).catch(error => {
            let [err, res] = error;
            reject(err)
			console.log(error[1].data)
			uni.showToast({
			    title: error,
				icon: 'none',
			    duration: 2000
			});
        })
    });
	
}
export default request